@media screen {

  html {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #237760;
    min-width: 23cm;
  }

  html, body, #root {
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  .App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 3em 0;
    flex: 1;
  }
  .page {
    box-shadow: rgba(50, 50, 50, 0.3) 0 3px 5px;
    margin: 2em;
  }



  .notes {
    margin-top: 2em;
    text-align: center;
    //color: #26292f;
    color: rgb(227, 227, 227);
    font-size: 0.8em;
    font-weight: 600;
    opacity: 0.5;
    font-family: 'Open Sans', sans-serif;
    p {
      margin: 2em 0;
    }

    a {
      color: inherit;
    }
    .red {
      color: #e30e0e;
    }
  }
}

@media print {
  .notes {
    display: none;

  }
}
